<template>
  <v-form>
    <v-card-text style="padding-bottom: 0;">
      <v-container>
        <v-text-field
          type="email"
          v-model="email"
          label="Email"
          color="teal"
          :error-messages="emailErrors"
          @input="$v.email.$touch()"
          @blur="$v.email.$touch()"
        ></v-text-field>
        <v-text-field
          type="password"
          autocomplete="password"
          v-model="password"
          label="Password"
          color="teal"
          :error-messages="passwordErrors"
          @input="$v.password.$touch()"
          @blur="$v.password.$touch()"
        ></v-text-field>
        <v-container style="padding-left: 0; padding-bottom: 0;">
          <v-btn
            @click="login"
            :loading="isLoading"
            :disabled="isLoading"
            color="teal"
            class="white--text"
            type="submit"
            block
          >
            Login
            <v-icon right dark>fa fa-sign-in-alt</v-icon>
          </v-btn>
          <p class="error--text" style="padding-top: 10px;">
            {{ serverMessage }}
          </p>
        </v-container>
      </v-container>
    </v-card-text>
  </v-form>
</template>

<script>
import firebase from "firebase";
import db from "./firebaseInit";

import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";

export default {
  name: "Luca",
  mixins: [validationMixin],
  data() {
    return {
      email: "",
      password: "",
      isLoading: false,
      serverMessage: ""
    };
  },
  methods: {
    login(e) {
      e.preventDefault();
      this.$v.$touch();
      this.serverMessage = "";
      if (!this.$v.$invalid) {
        this.isLoading = true;
        firebase
          .auth()
          .signInWithEmailAndPassword(this.email, this.password)
          .then(
            user => {
              this.isLoading = false;
              this.$router.push("/dashboard");
            },
            err => {
              if (err.code === "auth/user-not-found")
                this.serverMessage = `There is no account associated with ${this.email}`;
              else if (err.code === "auth/wrong-password")
                this.serverMessage =
                  "The password you have entered is incorrect.";
              else if (err.code === "auth/too-many-requests")
                this.serverMessage =
                  "Too many unsuccessful attempts. Please try again later.";
              else {
                console.log(err);
              }
              this.isLoading = false;
            }
          )
          .catch(err => {
            this.isLoading = false;
            console.log(err);
          });
      }
    }
  },
  validations: {
    email: { required },
    password: { required }
  },
  computed: {
    numCols() {
      return window.innerWidth >= 460 ? "6" : "12";
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.required && errors.push("Email is required");
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.required && errors.push("Password is required");
      return errors;
    }
  }
};
</script>

<style></style>



